.editor {
  grid-area: editor;
}

.headerButton {
  height: inherit;
  float: right;
  border: none;
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 2.8;
  text-transform: uppercase;
  background-color: inherit;
  letter-spacing: 0.08em;
  cursor: pointer;
}

.headerButton:disabled {
  color: #999;
  cursor: default;
}

.errorMarker {
  position: absolute;
  background: rgba(255, 0, 0, 0.4);
}



